body{
    font-family: sans-serif;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, 
     Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    box-sizing: border-box; 
    
}

a{
    text-decoration: none;
}

.box{
    padding-top: 30px;
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    font-size: 14px;   
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-radius: 20px;
    border:1px solid rgba(255, 255, 255, 0.18);
    box-shadow: 0 8px 40px 0 rgba(0, 0, 0, 0.37);
    
   
}

.log-in-container{
    background-image: url("./images/img1.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh;
    width:100%;
}
.field{
    margin: 20px 0;
    position: relative;
    font-size: 14px;
    width: 100%;
    text-overflow: ellipsis;
}
input{
    /* padding: 0px 0px 7px 9 px; */
    font-size: 12px;
    width: 16rem;
    height: 6 rem;
    outline: none;
    background: #fafafa;
    border-radius: 3px;
    border: 10px solid #fafafa;
    margin-bottom: 1rem;
    
}
label{
    position: absolute;
    pointer-events: none;
    left: 10px;
    padding-bottom: 15px;
    transform: translateY(10px);
    line-height: 6px;
    transition: all ease-out 0.1s;
    font-size: 14px;
    color: #999;
}
input::placeholder{
    visibility: hidden;
}
.log-in-form ::-moz-placeholder{
    color: transparent;
}
input:not(:placeholder-shown)+label{
    transform: translateY(0);
    font-size: 11px;
}
input:not(:placeholder-shown){
    padding-top: 14px;
    padding-bottom: -2px;
}
.login-button{
    text-align: center;
    width: 100%;
    padding: 0.5rem;
    border: 1px solid transparent;
    background-color: #3897f0; 
    color: #fff;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
}
.separator{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #999;
    margin-top: 6px;
}
.separator .line{
       height: 1px;
       width: 40%;
       background-color: #dbdbdb;
}
.other{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.forgot-password{
    font-size: 11px;
    color: #003569;
}
.signup{
    color: black;
    font-weight: 600;
}
.boxacc{
    color: black;
    font-weight: 600;
}
.logout-btn{
    align-items: left;
}
.navbar{
    align-items: left;
}































